.univ_begin {
  border: 2px solid #e2e2e2;
  border-radius: 20px;
  margin-top: 12px;
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ffecdc;
}
.univ_begin .univ_title {
  display: flex;
  justify-content: center;
  font-size: 24px;
}
.univ_begin .input_area {
  width: 100%;
  margin-top: 5%;
  padding-bottom: 10.2%;
}
.univ_begin .input_area .form_wrap {
  margin: 0 6.26%;
  padding: 7.6% 5.85%;
  border: 2px solid #ffa356;
  border-radius: 10px;
  background: #fff;
}
.univ_begin .input_area .form_wrap .input_set {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.univ_begin .input_area .form_wrap .input_set .form_txt {
  width: calc(100% - 57px);
}
.univ_begin .input_area .form_wrap .input_set .form_txt input[type=text] {
  width: 100%;
  height: 37px;
  margin-right: 1.4%;
  padding: 0 8px;
  border: 1px solid #acacac;
  border-radius: 5px;
  font-size: 14px;
  font-weight: normal;
}
.univ_begin .input_area .form_wrap .input_set .form_txt input[type=text]::placeholder {
  font-size: 14px;
  color: #000;
}
.univ_begin .input_area .form_wrap .input_set .form_txt input[type=text]:disabled {
  background: #ebebeb;
  color: #888888;
  border: 1px solid #ebebeb;
}
.univ_begin .input_area .form_wrap .input_set button {
  width: 52.5px;
  height: 37px;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .input_set button:disabled {
  background: #ebebeb;
  color: #888888;
}
.univ_begin .input_area .form_wrap .search_msg {
  padding: 4px 8px;
}
.univ_begin .input_area .form_wrap .search_msg > p {
  font-size: 12px;
  text-align: left;
  display: none;
  color: red;
}
.univ_begin .input_area .form_wrap .search_msg .list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0;
  border-bottom: 1px solid gray;
}
.univ_begin .input_area .form_wrap .search_msg .list li:last-child {
  border-bottom: unset;
}
.univ_begin .input_area .form_wrap .search_msg .list li p {
  font-size: 13px;
}
.univ_begin .input_area .form_wrap .search_msg .list li button {
  font-size: 13px;
  border-radius: 5px;
  padding: 6px;
  color: #fff;
  background: #464646;
}
.univ_begin .input_area .form_wrap .btn_reset {
  display: block;
  padding: 3% 5%;
  margin: 0 auto;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .btn_comp {
  display: block;
  width: 100%;
  padding: 5% 0%;
  margin-top: 3%;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .notice_txt {
  margin-top: 3%;
  text-align: left;
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
  color: #575757;
}
.univ_begin .join_area {
  width: 100%;
}
.univ_begin .join_area .join_wrap {
  margin: 0 6.26%;
  padding: 7.6% 5.85%;
  border: 2px solid #ffa356;
  border-radius: 10px;
  background: #fff;
}
.univ_begin .join_area .join_wrap .join_tit {
  text-align: center;
  font-size: 16px;
}
.univ_begin .join_area .join_wrap .btn_wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #ffa356;
  margin-top: 5%;
  border: 2px solid rgb(241, 224, 203);
  border-radius: 25px;
}
.univ_begin .join_area .join_wrap .btn_wrap .btn_join {
  margin: 10% 20% 30%;
  border-radius: 20px;
  font-size: 24px;
  transform: rotate(0deg) translate(10%, 10%);
  background: transparent;
  animation: shake 1s infinite 0.8s;
}
@-webkit-keyframes shake {
  50% {
    transform: rotate(15deg) translate(0%, 0%);
  }
}
@-moz-keyframes shake {
  50% {
    transform: rotate(15deg) translate(0%, 0%);
  }
}
@keyframes shake {
  50% {
    transform: rotate(15deg) translate(0%, 0%);
  }
}
.univ_begin .join_area .join_wrap .btn_wrap p {
  padding: 20px 0;
  font-size: 14px;
}
.univ_begin .join_area .join_wrap .name_wrap {
  margin: 5% 10%;
  padding: 3% 5%;
  border-radius: 10px;
  font-size: 14px;
  font-weight: bold;
  background: #ffa356;
}
.univ_begin .join_area .join_wrap .name_wrap p:first-child {
  margin-bottom: 2%;
}

.practice {
  width: 100%;
  height: 150px;
  background: #f37878;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.practice button {
  width: 30px;
  height: 30px;
}
.practice .alert_box {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 80%;
  height: 50%;
  background: yellowgreen;
  visibility: hidden;
}